.intro {
  opacity: 0;
  animation: fadeIn 500ms linear 2s normal forwards;
}

.intro2 {
  opacity: 0;
  animation: fadeIn2 500ms linear 0s normal forwards;
}

.introOut {
  opacity: 1;
  animation: fadeOut 500ms linear 2s normal forwards;
}

.displayON {
  opacity: 1;
}

.displayOFF {
  opacity: 0;
}

@keyframes fadeIn {
  0% {
    opacity: 1;
  }
  1% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  95% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    display: none;
  }
}/*# sourceMappingURL=animations.css.map */